  },
  mounted() {
    this.getRegions();
    console.log("current lang", this.currentLanguage)
  },
  computed: {
    ...mapGetters(['currentLanguage']),
  },
  methods: {
    toggleRegion(id) {
      this.regions = this.regions.map((region) => ({
        ...region,
        active: region.id === id ? !region.active : false,
      }));

      const selectedRegion = this.regions.find(region => region.id === id);
      if (selectedRegion) {
        this.$emit('region-selected', selectedRegion.id);
      }
    },
    getRegions() {
      api.get(`/etno-center-region/`, )
          .then((response) => {
            const serverData = response.data;
            this.updateRegionsWithServerData(serverData);
          })
          .catch((error) => {
            console.error('Ошибка при загрузке regions:', error);
          });
    },
    updateRegionsWithServerData(serverData) {
      serverData.forEach(serverItem => {
        const region = this.regions.find(region => region.id === serverItem.code);
        if (region) {
          switch (this.currentLanguage) {
            case 'kk':
              region.name = serverItem.titli_kk;
              break;
            case 'en':
              region.name = serverItem.titli_en;
              break;
            default:
              region.name = serverItem.titli_ru;
              break;
          }
        }
      });
    }
  },
};
